import React from 'react';
import { PageProps, Link, graphql } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';

type DataProps = {
	site: {
		buildTime: string;
	};
};

const Structures: React.FC<PageProps<DataProps>> = ({ data, path }) => (
	<Layout>
		<SEO title="Monte Rosa's Structures and Architecture" />
		<h1>Structures and Architecture</h1>
		<p>
			Around 1847 and at the age of 50, William “Extra Billy” Smith (1797-1887) purchased a house with land just
			off Culpeper Street on the south side of Warrenton, Virginia. He set about greatly enlarging the house which
			he named Monte Rosa. It would later be renamed Neptune Lodge by James Kerfoot Maddux (1853-1930) who
			purchased the property in September, 1895. Maddux in his own right was also a prominent member of the
			Warrenton community. Mr. Maddux renamed the property in honor of one of his racehorses. The original
			property was located in the southern portion of the Town of Warrenton, with parts of it extending outside of
			the town’s limits. It included the Governor’s mansion, stables, an assortment of other farm related
			structures and approximately 220 acres of fields and woods. Today the property is in the heart of the
			Historic district of Warrenton and includes approximately seven acres of land consisting of the Governor’s
			mansion, stables, Extra Billy’s “office building”.
		</p>
		<Link to="/">Go back to the homepage</Link>
	</Layout>
);

export default Structures;

export const query = graphql`
	{
		site {
			buildTime(formatString: "YYYY-MM-DD hh:mm a z")
		}
	}
`;
